:global {
  @import url('https://fonts.googleapis.com/css?family=Roboto:400,400i,500,500i,700,700i');

  body {
    font-family: roboto, sans-serif;
  }

  .segment {
    box-shadow: inset 2pt 2pt 10pt rgba(0, 0, 0, .2) !important;
  }

  circle {
    opacity: .5;
  }

  .axis path {
    shape-rendering: crispEdges;
    opacity: .5;
  }

  .axis text {
    font-size: 8pt;
    font-family: 'roboto', sans-serif;
    opacity: .5;
    user-select: none;
  }

  svg.scatter-plot {
    overflow: visible !important;
  }

  .axis text.label {
    font-family: 'roboto', sans-serif;
    font-size: 12px;
    letter-spacing: 1px;
    font-weight: 500;
    opacity: 1;
  }
}
